@import "../../scss/variables";

.addui-slider {
	display: block;
	box-sizing: border-box;
	* {
		box-sizing: border-box;
	}
	&:not(.addui-slider-isrange) {
		cursor: pointer;
	}
	.addui-slider-track {
		display: block;
		position: relative;
		background: #f3f7fd;
		border-radius: 4px;
		.addui-slider-range {
			display: block;
			border-radius: 4px;
			position: absolute;
		}
		.addui-slider-handle {
			display: block;
			width: 50px;
			height: 50px;
			background: rgba(5, 117, 230, 0);
			box-shadow: 0 0 5px rgba(5, 117, 230, 0);
			position: absolute;
			border-radius: 50px;
			cursor: pointer;
			transition: background 1s, box-shadow 1s;
			z-index: 0;
			.addui-slider-value {
				display: block;
				float: right;
				background: rgba(5, 117, 230, 0);
				width: 50px;
				height: 50px;
				border-radius: 50%;
				margin-top: 0px;
				color: white;
				opacity: 0;
				pointer-events: none;
				transition: opacity 1s, background 1s, transform 1s, margin 1s, border-radius 1s;
				transform: scale(1, 0) rotate(-135deg);
				transform-style: preserve-3d;
				position: absolute;
				z-index: 1;
				span {
					display: block;
					transform: rotate(135deg);
					width: 100%;
					height: 100%;
					text-align: center;
					line-height: 50px;
					position: absolute;
					z-index: 1;
				}
			}
			&:hover {
				box-shadow: 0 0 5px rgba(5, 117, 230, 0.5);
				.addui-slider-value {
					background: rgba(5, 117, 230, 0.5);
					opacity: 1;
					transform: scale(1, 1) rotate(-135deg);
					margin-top: -60px;
					border-radius: 0 50% 50% 50%;
				}
			}
			&:after {
				display: block;
				content: " ";
				width: 25px;
				height: 25px;
				border-radius: 25px;
				position: relative;
				top: 13px;
				left: 13px;
			}
		}
		.addui-slider-handle.addui-slider-handle-active {
			background: rgb(5, 117, 230, 0.5);
			box-shadow: 0 0 5px rgb(5, 117, 230, 0.5);
			.addui-slider-value {
				background: rgb(5, 117, 230, 0.75);
				opacity: 1;
				transform: scale(1, 1) rotate(-135deg);
				margin-top: -60px;
				border-radius: 0 50% 50% 50%;
			}
		}
	}
}
.addui-slider.addui-slider-vertical {
	height: calc(100% - 50px);
	width: 50px;
	margin: 25px 16px;
	margin: 0 auto;
	.addui-slider-track {
		height: 100%;
		width: 4px;
		top: 0;
		left: 23px;
		.addui-slider-range {
			width: 4px;
			height: auto;
		}
		.addui-slider-handle {
			left: -24px;
			top: auto;
			transform: translateY(50%);
		}
		.addui-slider-handle.addui-slider-handle-l {
			z-index: 2;
		}
	}
}
.addui-slider.addui-slider-horizontal {
	height: 50px;
	width: calc(100% - 50px);
	margin: 16px 25px;
	.addui-slider-track {
		height: 4px;
		width: 100%;
		top: 23px;
		left: 0;
		.addui-slider-range {
			width: auto;
			height: 4px;
		}
		.addui-slider-handle {
			left: auto;
			top: -24px;
			transform: translateX(-50%);
		}
	}
}

